import { Component, OnInit,ViewChild } from '@angular/core';
import { Router, ActivatedRoute} from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import {LoopBackAuth, BooksApi,ContainerApi }  from '../../../../shared/loopback-sdk';
import {CookieService} from 'angular2-cookie/core';
import {Headers, Http, Response} from '@angular/http'; 
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window:any;
declare var hostname:any;
declare var sdkPrefix;
@Component({
  templateUrl: 'edit-page.component.html'
})
export class EditPageComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('pckeditor') pckeditor: NgModel;
	
	display:boolean=false;result:any;
	submitted:boolean=false;
	pageHeading:any;
	busy:any;
	id:any;
	userDetails:any;
	rolename:any;
	description:any;
	chapterId:any;
	pagePriority:Number=0;
	chapterName:any;
	loading:boolean=false;
	constructor(private loopAuth:LoopBackAuth, private route:ActivatedRoute, private http:Http,private _cookieService:CookieService,private books:BooksApi, private router:Router, private containerApi:ContainerApi, private share:ShareService) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
	}

	ngOnInit(): void {
		this.loading=true;
		var dataArrObj = {
			"id":this.id
		}
		this.books.getPages(dataArrObj).subscribe(data=>{
			this.loading = false;
			if(data){
				let result = data.result[0];
				let dataDesc = result.content;
				this.replaceImageUrl(dataDesc).then((resultData)=>{
					this.pageHeading = result.page_heading;
					this.chapterId = result.chapter_id;
					this.chapterName = result.chapter_name;
					this.pagePriority = result.priority;
					this.description = resultData;
					console.log("dataDesc", this.description);
				});
			}
		},
		err=>{
			this.loading = false;
		},
		()=>{});
	}

	async submit() {
		this.submitted=true;
		if(this.name.valid && this.pckeditor.valid){
			this.loading = true;
			//function call to change base64 format data to Image
			console.log("description", this.description);
			let description = await this.share.parsePageToReplaceImg(this.description);
			console.log("description after",description);
			var dataArrObj = {
				"page_heading":this.pageHeading,
				"chapter_id":this.chapterId,
				"content": description,
				"priority":this.pagePriority
			}

			this.books.addEditPages(dataArrObj, this.id).subscribe(
				data=>{
					this.loading = false;
					if(data){
						console.log("data", data);
						if(data.result.id > 0){
							this.display=true;
							this.result = 'success';
						}
					}else{
						this.display=true;
						this.result = 'Something went wrong.';
					}
				},
				err=>{
					console.log("err", err);
					this.loading = false;
				},
				()=>{

				}
			)
		}
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["books/view-chapter", this.chapterId]);
	}

	replaceImageUrl(content){
		return new Promise((resolve,reject)=>{
			const parser = new DOMParser();
			let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
			let content1 = parser.parseFromString(content,"text/html");
			if(content1.images.length > 0){
				for(let i=0;i<content1.images.length;i++){
					let tempImageSrc = "";
					let imageSrc = "";
					tempImageSrc = content1.images[i].src;
					imageSrc = content1.images[i].src+'?access_token='+loginFlagToken;
					content = content.replace(tempImageSrc,imageSrc);
				}
				resolve(content);
			}else{
				resolve(content);
			}
		});
	}
}



